import Config from '../../../config.js';
import CallState from "../engine/callState";
import avenginekit from './avenginekitImpl'
import CallEndReason from "../engine/callEndReason";
import CallByeMessageContent from "../messages/callByeMessageContent";
import PeerConnectionClient from "./PeerConnectionClientImpl";
import {currentWindow, desktopCapturer} from '../../../platform.js'
import CallSession from "../engine/CallSession";
import {gt,numberValue} from '../../util/longUtil.js'

// voip window
export default class CallSessionImpl extends CallSession {
    static iceServers = [{
        urls: [Config.ICE_ADDRESS],
        username: Config.ICE_USERNAME,
        credential: Config.ICE_PASSWORD
    }];
    callId;
    joinTime = 0;
    acceptTime = 0;

    connectedTime;
    endTime;
    endReason;
    conversation;

    status = 0;

    initiatorId;
    participantUserInfos;
    selfUserInfo;

    moCall; // true, outgoing; false, incoming
    cameraVideoStream;
    screenShareStream;
    remoteStream;
    callTimer;

    sessionCallback;

    peerConnectionClientMap;

    groupMemberUserInfos;

    singleCall;

    static newSession(conversation, initiatorId, callId, audioOnly, sessionCallback) {
        let session = new CallSessionImpl();
        session.conversation = conversation;
        session.initiatorId = initiatorId;
        session.callId = callId;
        session.audioOnly = audioOnly;
        session.sessionCallback = sessionCallback;
        return session;
    }

    getClient(userId) {
        return this.peerConnectionClientMap.get(userId);
    }

    getParticipantIds() {
        let ids = [];
        this.participantUserInfos.forEach(u => {
            ids.push(u.uid);
        });
        return ids;
    }

    setAcceptTime(timestamp) {
        this.acceptTime = timestamp;
        this.tryStartMedia();
    }

    setUserAcceptTime(userId, timestamp) {
        console.log('setUserAcceptTime', userId, timestamp);
        let client = this.getClient(userId);
        client.acceptTime = timestamp;
        this.tryStartMedia()
    }

    setUserJoinTime(userId, timestamp) {
        let client = this.getClient(userId);
        client.joinTime = timestamp;
    }

    tryStartMedia() {
        if(gt(this.acceptTime, 0)){
            this.peerConnectionClientMap.forEach((client, userId) => {
                if (gt(client.acceptTime, 0)
                    && (client.status === CallState.STATUS_INCOMING || client.status === CallState.STATUS_OUTGOING)) {
                    if (gt(this.acceptTime, client.acceptTime)) {
                        this.startMedia(userId, true);
                    } else {
                        this.startMedia(userId, false);
                    }
                }
            }, this);
        }
    }

    getPeerConnection(userId) {
        let client = this.peerConnectionClientMap.get(userId);
        return client.peerConnection;
    }

    answerCall(audioOnly) {
        if (this.status !== CallState.STATUS_INCOMING) {
            return;
        }
        // 
        if (this.audioOnly && !audioOnly) {
            audioOnly = true;
        }

        this.audioOnly = audioOnly;
        avenginekit.answerCurrentCall();
    }

    setState(status) {
        if(this.status === status){
            if(status === CallState.STATUS_CONNECTED && this.sessionCallback){
                this.sessionCallback.didChangeState(status);
            }
            return;
        }
        if(this.status === CallState.STATUS_CONNECTED && status === CallState.STATUS_CONNECTING){
            return;
        }

        this.status = status;
        console.log('set status', status, this.startMsgUid);

        if(this.startMsgUid){
            let content = {
                audioOnly : this.audioOnly,
            };
            content.status = this.endReason;
            if(status === CallState.STATUS_CONNECTED){
                content.connectTime = new Date().valueOf();
            }else {
                content.endTime = new Date().valueOf();
            }
            avenginekit.updateCallStartMessageContent(this.startMsgUid, content)
        }

        if (this.sessionCallback) {
            this.sessionCallback.didChangeState(status);
        }
    }

    setAudioOnly(audioOnly) {
        this.audioOnly = audioOnly;
        if (this.sessionCallback) {
            this.sessionCallback.didChangeMode(audioOnly);
        }
    }


    playIncomingRing() {
        // TODO
        //
    }

    stopIncomingRing() {
        // TODO
        ////
    }

    /**
     *
     * @param moCall
     * @param selfUserInfo
     * @param participantUserInfos
     * @param groupMemberUserInfos 
     */
    initSession(moCall, selfUserInfo, participantUserInfos, groupMemberUserInfos) {
        this.moCall = moCall;
        this.selfUserInfo = selfUserInfo;
        this.participantUserInfos = participantUserInfos;
        this.singleCall = participantUserInfos.length === 1;
        this.groupMemberUserInfos = groupMemberUserInfos;
        let initiatorUserInfo = selfUserInfo;
        if (!moCall) {
            initiatorUserInfo = participantUserInfos.filter(u => u.uid === this.initiatorId)[0];
        }

        this.sessionCallback && this.sessionCallback.onInitial(this, selfUserInfo, initiatorUserInfo, participantUserInfos, groupMemberUserInfos);

        let participants = [];
        participantUserInfos.forEach(u => {
            participants.push(u.uid);
        });
        this.initParticipantClientMap(participants);

        if (moCall) {
            this.setState(CallState.STATUS_OUTGOING);
            this.startPreview(this.audioOnly);
        } else {
            this.setState(CallState.STATUS_INCOMING);
            this.playIncomingRing();
        }
        this.callTimeout();
    }

    // TODO delta
    callTimeout = ()=>{
        let now = new Date().valueOf();
        // console.log('check call timeout');
        if(!this.callTimer){
            this.callTimer = setInterval(this.callTimeout, 1 * 1000)
        }
        if(this.status === CallState.STATUS_INCOMING && gt(this.joinTime, 0)){
            if(now - numberValue(this.joinTime) > 60 * 1000){
                this.endCall(CallEndReason.REASON_Timeout);
            }
        }else if(this.status !== CallState.STATUS_IDLE){
            this.peerConnectionClientMap.forEach((client, userId) => {
               if(client.status !== CallState.STATUS_CONNECTED && gt(client.joinTime, 0) && now - numberValue(client.joinTime) > 60 * 1000){
                   this.endUserCall(userId, CallEndReason.REASON_Timeout);
               }
            })
        }
    }

    initParticipantClientMap(participants) {
        console.log('initParticipantClientMap', participants);
        if (!this.peerConnectionClientMap) {
            this.peerConnectionClientMap = new Map();
        }
        participants.forEach(uid => {
            let client = new PeerConnectionClient(uid, this);
            if (uid === this.selfUserInfo.uid) {
                client.status = CallState.STATUS_OUTGOING;
            } else {
                client.status = CallState.STATUS_INCOMING;
            }
            this.peerConnectionClientMap.set(uid, client);
        }, this);
    }


    // PC/Web
    inviteNewParticipants(newParticipantIds) {
        if (!newParticipantIds.length) {
            return;
        }
        newParticipantIds = newParticipantIds.filter(uid => {
            return uid !== this.selfUserInfo.uid && this.participantUserInfos.findIndex((u) => u.uid === uid) < 0;
        });
        if (!newParticipantIds.length) {
            return;
        }

        this.singleCall = false;
        avenginekit.inviteNewParticipants(newParticipantIds);
    }

    getExistParticipantsStatus() {
        let statuses = [];
        statuses.push({
            userId: this.selfUserInfo.uid,
            acceptTime: numberValue(this.acceptTime),
            joinTime: numberValue(this.joinTime),
            videoMuted: this.videoMuted
        })

        this.participantUserInfos.forEach(u => {
            let client = this.getClient(u.uid);
            statuses.push({
                userId: client.userId,
                acceptTime: numberValue(client.acceptTime),
                joinTime: numberValue(client.joinTime),
                videoMuted: client.videoMuted
            });
        }, this);
        return statuses;
    }


    didAddNewParticipants(newParticipants, newParticipantUserInfos) {
        console.log('didAddNewParticipants', newParticipants, newParticipantUserInfos)
        newParticipants.forEach(p => {
            let client = new PeerConnectionClient(p, this);
            client.status = CallState.STATUS_INCOMING;
            this.peerConnectionClientMap.set(p, client);
        }, this);

        newParticipantUserInfos.forEach(u => {
            this.participantUserInfos.push(u);
            this.sessionCallback && this.sessionCallback.didParticipantJoined(u.uid, u);
        }, this);
    }

    updateExistParticipant(existParticipants, joinTime) {
        existParticipants.forEach(p => {
            let client = this.getClient(p.userId);
            client.status = CallState.STATUS_INCOMING;
            client.joinTime = joinTime;
            client.videoMuted = p.videoMuted;
            client.acceptTime = p.acceptTime;
        });
    }


    updateVideoMute(userId, videoMuted){
        let client = this.getClient(userId);
        if(client && client.videoMuted !== videoMuted){
            client.videoMuted = videoMuted;
            this.sessionCallback && this.sessionCallback.didVideoMuted(userId, videoMuted);
        }
    }

    async startPreview(audioOnly) {
        console.log('start preview');
        try {
            const stream = await navigator.mediaDevices.getUserMedia({audio: true, video: !audioOnly});
            // const stream = await navigator.mediaDevices.getDisplayMedia({audio: true, video: !audioOnly});
            console.log('Received local stream', stream);
            if (this.sessionCallback) {
                this.sessionCallback.didCreateLocalVideoTrack(stream);
            }
            // this.localVideo.srcObject = stream;
            this.cameraVideoStream = stream;

            const videoTracks = this.cameraVideoStream.getVideoTracks();
            if (!audioOnly) {
                if (videoTracks && videoTracks.length > 0) {
                    console.log(`Using video device: ${videoTracks[0].label}`);
                }
            } else {
                if (videoTracks && videoTracks.length > 0) {
                    videoTracks.forEach(track => track.stop());
                }
            }

            const audioTracks = this.cameraVideoStream.getAudioTracks();
            if (audioTracks.length > 0) {
                console.log(`Using audio device: ${audioTracks[0].label}`);
            }
        } catch (e) {
            console.error('getUserMedia error', e);
            alert(`getUserMedia() error: ${e.name}`);
            this.endCall(CallEndReason.REASON_MediaError);
        }
    }


    async startMedia(userId, isInitiator) {
        console.log('start media', isInitiator);
        this.setState(CallState.STATUS_CONNECTING);
        this.startTime = window.performance.now();
        let client = this.getClient(userId);
        client.status = CallState.STATUS_CONNECTING;
        if (!this.cameraVideoStream) {
            this.startPreview(this.audioOnly).then(() => {
                console.log('start pc 0');
                this.createPeerConnection(userId, isInitiator);
            });
        } else {
            console.log('start pc 1');
           await this.createPeerConnection(userId, isInitiator);
        }
    }

    /**
     * @param {[string]} screenwindow
     * @return {Promise<DesktopCapturerSource[]>}
     */
    getDesktopSources(types){
        if(desktopCapturer){
            return desktopCapturer.getSources({ types: types });
        }
        return null;
    }

    /**
     *
     * @param desktopShareOptions {          sourceId: source.id,
                                        minWidth: 1280,
                                        maxWidth: 1280,
                                        minHeight: 720,
                                        maxHeight: 720}
     * @return {Promise<void>}
     */
    async startScreenShare(desktopShareOptions){
        console.log('start screen share');
        if(!this.screenShareStream){
            console.log('desktopCapturer ', desktopCapturer);
            if(desktopCapturer){
                this.screenShareStream = await navigator.mediaDevices.getUserMedia({
                    audio: false,
                    video:{
                        mandatory:{
                            chromeMediaSource: 'desktop',
                            ... desktopShareOptions
                        }
                    }
                });
                console.log('desktopCapturer screen share stream', this.screenShareStream)
            }else {
                this.screenShareStream = await navigator.mediaDevices.getDisplayMedia({audio: false, video: true});
            }
        }

        if (this.sessionCallback) {
            this.sessionCallback.didCreateLocalVideoTrack(this.screenShareStream);
        }

        if (gt(this.acceptTime, 0)) {
            this.peerConnectionClientMap.forEach((client, userId) => {
                if(client.videoSender){
                    client.videoSender && client.videoSender.replaceTrack(this.screenShareStream.getVideoTracks()[0]);
                }
            }, this);
        }
    }

    isScreenSharing(){
        return this.screenShareStream !== null;
    }

    stopScreenShare(){
        this.screenShareStream = null;

        if (this.sessionCallback && this.cameraVideoStream) {
            this.sessionCallback.didCreateLocalVideoTrack(this.cameraVideoStream);
        }
        if (gt(this.acceptTime, 0)) {
            this.peerConnectionClientMap.forEach((client, userId) => {
                if(client.videoSender){
                    client.videoSender && client.videoSender.replaceTrack(this.cameraVideoStream.getVideoTracks()[0]);
                }
            }, this);
        }
    }

    async createPeerConnection(userId, isInitiator) {
        let client = this.getClient(userId);
        let configuration = this.getSelectedSdpSemantics();
        configuration.iceServers = CallSession.iceServers;
        console.log('RTCPeerConnection configuration:', configuration);

        let pc = new RTCPeerConnection(configuration);
        client.peerConnection = pc;
        client.isInitiator = isInitiator;

        console.log('Created local peer connection object pc');
        pc.addEventListener('icecandidate', e => this.onIceCandidate(userId, pc, e));

        pc.addEventListener('iceconnectionstatechange', e => this.onIceStateChange(userId, pc, e));
        pc.addEventListener('track', e => this.gotRemoteStream(userId, e));
        pc.addEventListener('connectionstatechange', e => this.onConnectionStateChange(userId, e));

        let stream = this.screenShareStream ? this.screenShareStream : this.cameraVideoStream;
        if (!this.audioOnly) {
            // this.localStream.getTracks().forEach(track => pc.addTrack(track, this.localStream), this);

            pc.addTrack(stream.getAudioTracks()[0], stream);
            client.videoSender = pc.addTrack(stream.getVideoTracks()[0], stream);
        } else {
            // client.videoSender = pc.addTrack(this.localStream.getVideoTracks()[0], this.localStream);

            stream.getAudioTracks().forEach(track => pc.addTrack(track, stream), this);
        }
        console.log('Added local stream to pc');

        if (isInitiator) {
            try {
                console.log('pc createOffer start');
                let offerOptions = {
                    offerToReceiveAudio: true,
                    offerToReceiveVideo: !this.audioOnly
                };
                const offer = await pc.createOffer(offerOptions);
                let mutableOffer = JSON.parse(JSON.stringify(offer));
                mutableOffer.type = 'offer';
                await this.onCreateOfferSuccess(userId, offer);
            } catch (e) {
                this.onCreateSessionDescriptionError(userId, e);
            }
        }

        console.log('createPeerConnection', userId, this.getClient(userId))

    }


    getSelectedSdpSemantics() {
        // const sdpSemanticsSelect = document.querySelector('#sdpSemantics');
        // const option = sdpSemanticsSelect.options[sdpSemanticsSelect.selectedIndex];
        // return option.value === '' ? {} : { sdpSemantics: option.value };
        return {};
    }

    call() {
        console.log('voip on call button click');
        this.stopIncomingRing();

        console.log('on call button call');
        this.answerCall(this.audioOnly);
    }

    onCreateSessionDescriptionError(userId, error) {
        console.log('Failed to create session description');
        // console.log(`Failed to create session description: ${error.toString()}`);
        this.endUserCall(userId, CallEndReason.REASON_MediaError);
    }

    async onReceiveRemoteCreateOffer(userId, desc) {
        console.log('pc setRemoteDescription start');
        let pc = this.getPeerConnection(userId);
        console.log('onReceiveRemoteCreateOffer', userId, pc)
        try {
            await pc.setRemoteDescription(desc);
            this.onSetRemoteSuccess(pc);
        } catch (e) {
            this.onSetSessionDescriptionError(userId, e);
        }

        console.log('pc createAnswer start');
        // Since the 'remote' side has no media stream we need
        // to pass in the right constraints in order for it to
        // accept the incoming offer of audio and video.
        try {
            const answer = await pc.createAnswer();
            let mutableAnswer = JSON.parse(JSON.stringify(answer));
            mutableAnswer.type = 'answer';
            await this.onCreateAnswerSuccess(userId, answer);
        } catch (e) {
            this.onCreateSessionDescriptionError(e);
        }
    }

    async onCreateOfferSuccess(userId, desc) {
        console.log(`Offer from pc\n${desc.sdp}`);
        console.log('pc setLocalDescription start');
        let pc = this.getPeerConnection(userId);
        try {
            await pc.setLocalDescription(desc);
            this.onSetLocalSuccess(pc);
        } catch (e) {
            this.onSetSessionDescriptionError(userId, e);
        }

        console.log(desc);
        avenginekit.onCreateAnswerOffer(userId, desc);
    }

    onSetLocalSuccess(pc) {
        console.log(`setLocalDescription complete`);
    }

    onSetRemoteSuccess(pc) {
        console.log(`setRemoteDescription complete`);
    }

    onSetSessionDescriptionError(userId, error) {
        console.log(`Failed to set session description: ${userId}`);
        console.error(error);
        this.endUserCall(userId, CallEndReason.REASON_MediaError)
    }

    gotRemoteStream = (userId, e) => {
        if (this.remoteStream !== e.streams[0]) {
            if (this.sessionCallback) {
                this.sessionCallback.didReceiveRemoteVideoTrack(userId, e.streams[0]);
            }
            // this.remoteVideo.srcObject = e.streams[0];
            console.log('pc received remote stream', e.streams[0]);
        }
    };

    async onReceiveRemoteAnswerOffer(userId, desc) {
        console.log('pc setRemoteDescription start');
        try {
            let pc = this.getPeerConnection(userId);
            await pc.setRemoteDescription(desc);
            this.onSetRemoteSuccess(pc);
        } catch (e) {
            this.onSetSessionDescriptionError(userId, e);
        }
    }

    async setRemoteIceCandidate(userId, message) {
        console.log('handle the candidiated');
        this.onReceiveRemoteIceCandidate(userId, message);
    }

    async onCreateAnswerSuccess(userId, desc) {
        console.log(`Answer from pc:\n${desc.sdp}`);
        console.log('pc setLocalDescription start');
        try {
            let pc = this.getPeerConnection(userId);
            await pc.setLocalDescription(desc);
            this.onSetLocalSuccess(pc);
        } catch (e) {
            this.onSetSessionDescriptionError(userId, e);
        }
        console.log(desc);
        avenginekit.onCreateAnswerOffer(userId, desc);
    }

    async onReceiveRemoteIceCandidate(userId, message) {
        console.log('on receive remote ice candidate');
        let pc = this.getPeerConnection(userId);
        await pc.addIceCandidate(message);
    }

    onIceCandidate = (userId, pc, event) => {
        if (!event.candidate) {
            return;
        }
        try {
            let candidate = {
                type: 'candidate',
                label: event.candidate.sdpMLineIndex,
                id: event.candidate.sdpMid,
                candidate: event.candidate.candidate
            };
            avenginekit.onIceCandidate(userId, candidate);
            this.onAddIceCandidateSuccess(userId, pc);
        } catch (e) {
            this.onAddIceCandidateError(userId, pc, e);
        }
        console.log(`ICE candidate:\n${event.candidate ? event.candidate.candidate : '(null)'}`);
    };

    onAddIceCandidateSuccess(userId, pc) {
        console.log(`send Ice Candidate success`);
    }

    onAddIceCandidateError(userId, pc, error) {
        console.log(`failed to add ICE Candidate: ${userId} ${error.toString()}`);
        this.endUserCall(userId, CallEndReason.REASON_MediaError);
    }

    onConnectionStateChange(userId, pc, event){
        console.log('onConnectionStateChange', userId, pc, event);
        // for (PeerConnectionClient client : currentSession.peerConnectionClientMap.values()) {
        //     if (client.getPeerConnection() == peerConnection) {
        //         CallEndReason reason = MediaError;
        //         if ("Timeout".equals(description)) {
        //             reason = currentSession.isInitiator() ? RemoteTimeout : Timeout;
        //         } else if ("ConnectingTimeout".equals(description)) {
        //             reason = RemoteNetworkError;
        //         }
        //         currentSession.endCall(client.userId, reason);
        //         break;
        //     }
        // }
        //
        // if (currentSession.callback != null) {
        //     currentSession.callback.didError(description);
        // }
    }

    onIceStateChange = (userId, pc, event) => {
        if (pc) {
            let client = this.getClient(userId);
            console.log(`ICE state: ${pc.iceConnectionState}`, pc);
            console.log('ICE state change event: ', event);
            if (pc.iceConnectionState === 'connected') {
                this.setState(CallState.STATUS_CONNECTED);
                client.status = CallState.STATUS_CONNECTED;
            }
            if (pc.iceConnectionState === 'disconnected') {
                this.endUserCall(userId, CallEndReason.REASON_MediaError);
            } else if (pc.iceConnectionState === 'connected') {
                this.setState(CallState.STATUS_CONNECTED);
            } else if (pc.iceConnectionState === 'failed') {
                this.endUserCall(userId, CallEndReason.REASON_MediaError);
            }
        }
    };

    hangup() {
        console.log('Ending call');
        this.endCall(CallEndReason.REASON_Hangup);
    }

    triggerMicrophone() {
        console.log('trigger microphone');
        if (this.cameraVideoStream) {
            const audioTracks = this.cameraVideoStream.getAudioTracks();
            if (audioTracks && audioTracks.length > 0) {
                audioTracks[0].enabled = !audioTracks[0].enabled;
                this.muted = !this.muted;
            }
        }
    }

    // 
    downgrade2Voice(inDirection = false) {
        if (this.status !== CallState.STATUS_CONNECTED) {
            return
        }

        const localVideoTracks = this.cameraVideoStream.getVideoTracks();
        if (localVideoTracks && localVideoTracks.length > 0) {
            localVideoTracks.forEach(track => track.stop());
        }

        if(inDirection){
        this.downToVoice();
        }
    }

    // 
    downToVoice() {
        console.log('down to voice');
        this.stopIncomingRing();
        if (this.status === CallState.STATUS_INCOMING) {
            this.answerCall(true);
            return;
        }

        if (this.status !== CallState.STATUS_CONNECTED) {
            return;
        }

        if (this.audioOnly) {
            return;
        }
        this.setAudioOnly(true);
        avenginekit.downgrade2VoiceCall();
    }

    setVideoEnabled (enable) {
        if(this.audioOnly){
            return;
        }

        this.videoMuted = !enable;
        const localVideoTracks = this.cameraVideoStream.getVideoTracks();
        if (localVideoTracks && localVideoTracks.length > 0) {
            localVideoTracks.forEach(track => track.enabled = enable);
        }
        avenginekit.setVideoEnabled(enable);
    }

    endMedia() {
        console.log('Ending media');
        this.setState(CallState.STATUS_IDLE);
        this.stopIncomingRing();//

        if (this.cameraVideoStream) {
            if (typeof this.cameraVideoStream.getTracks === 'undefined') {
                // Support legacy browsers, like phantomJs we use to run tests.
                this.cameraVideoStream.stop();
            } else {
                this.cameraVideoStream.getTracks().forEach(function (track) {
                    track.stop();
                });
            }
            this.cameraVideoStream = null;
        }

        // TODO close all
        // if (this.pc) {
        //     this.pc.close();
        //     this.pc = null;
        // }

        // 
        // setRemoteDescription
        // eslint-disable-next-line no-const-assign
        if(Config.ENABLE_VOIP_DEBUG_MODE){
            return;
        }
        setTimeout(() => {
            if (currentWindow) {
                currentWindow.close();
            } else {
                window.close();
            }
        }, Config.VOIP_WINDOW_CLOSE_DELAY ? Config.VOIP_WINDOW_CLOSE_DELAY * 1000 : 2000);
    }

    endUserCall(userId, reason) {
        console.log('endUserCall', userId, reason);
        if (userId === this.selfUserInfo.uid) {
            this.endCall(reason);
            return;
        }

        let client = this.getClient(userId);
        this.peerConnectionClientMap.delete(userId);
        this.participantUserInfos = this.participantUserInfos.filter(info => info.uid !== userId);
        if (client) {
            if (client.peerConnection) {
                client.peerConnection.close();
            }
            if (this.sessionCallback && this.peerConnectionClientMap.size > 0) {
                this.sessionCallback.didParticipantLeft(userId, reason);
            }
        }

        if (this.peerConnectionClientMap.size === 0) {
            if(this.conversation.type === 0 || this.singleCall){
                this.endCall(reason);
            }else {
                this.endCall(CallEndReason.REASON_AllLeft);
            }
        }
    }

    endCall(reason) {
        this.endReason = reason;
        if (this.status === CallState.STATUS_IDLE) {
            return;
        }

        this.sessionCallback  && this.sessionCallback.didCallEndWithReason(reason);

        this.setState(CallState.STATUS_IDLE);

        let byeMessage = new CallByeMessageContent();
        byeMessage.callId = reason === CallEndReason.REASON_AcceptByOtherClient ? '' : this.callId;
        byeMessage.reason = CallEndReason.REASON_AcceptByOtherClient;
        avenginekit.sendSignalMessage(byeMessage, this.getParticipantIds(), false);

        this.endTime = (new Date()).valueOf();
        this.endMedia();

        avenginekit.currentSession = null;
        clearInterval(this.callTimer)
    }
}
